import { Switch, Route } from 'react-router-dom';

import Home from './Home';
import Shop from './Shop';
import About from './About';

import 'css/Main.css';

function Main() {
  return (
    <main>
      <Switch>
        <Route path="/" exact>
          <Home />
        </Route>
        <Route path="/shop">
          <Shop />
        </Route>
        <Route path="/about">
          <About />
        </Route>
      </Switch>
    </main>
  );
}

export default Main;
