import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { deleteProduct } from 'store/actionCreators';

import "css/BoughtProducts.css";

function BoughtProducts() {
  const bought = useSelector(state => state.bought);
  const dispatch = useDispatch();

  const handleDeleteProduct = (name) => {
    dispatch(deleteProduct({ name }));
  };

  const renderedBought = bought.map(({ name, amount }) => (
    <li className="bought-products__product border">
      <h2>{name}</h2>
      <br/><hr/><br/>
      <div>
        Amount: {amount}
      </div>
      <button
        className="product__delete-btn border-none"
        onClick={() => handleDeleteProduct(name)}
      >
        Delete
      </button>
    </li>
  ));

  if (!bought.length) {
    return (
      <span className="mr-top-left">No products bought</span>
    );
  }

  return (
    <ul id="bought-products">
      {renderedBought}
    </ul>
  );
}

export default BoughtProducts;
