import { ADD_PRODUCT } from "store/actionTypes";

const initial = [
  {
    name: 'Apple',
    price: 20
  },
  {
    name: 'Banana',
    price: 12
  },
  {
    name: 'Cucember',
    price: 100
  }
];

function productsReducer(state = initial, action) {
  switch (action.type) {
    case ADD_PRODUCT:
      return [...state, action.payload];
  }

  return state;
}

export default productsReducer;
