import { BUY_PRODUCT, DELETE_PRODUCT } from "store/actionTypes";

const initial = [
  {
    name: 'Apple',
    amount: 10
  },
  {
    name: 'Banana',
    amount: 30
  }
]


function boughtReducer(state = initial, action) {
  let newProducts;
  switch (action.type) {
    case BUY_PRODUCT:
      newProducts = [...state];
      for (let i = 0; i < newProducts.length; i++) {
        if (action.payload.name === newProducts[i].name) {
          newProducts[i].amount++;
          return newProducts;
        }
      }
      newProducts.push({ name: action.payload.name, amount: 1 });
      return newProducts;
    case DELETE_PRODUCT:
      newProducts = [...state];
      for (let i = 0; i < newProducts.length; i++) {
        if (action.payload.name === newProducts[i].name) {
          newProducts.splice(i, 1);
        }
      }
      return newProducts;
  }
  return state;
}

export default boughtReducer;
