import { ADD_PRODUCT, BUY_PRODUCT, DELETE_PRODUCT, SET_CASH } from "./actionTypes";

export function addProduct(payload) {
  return {
    type: ADD_PRODUCT,
    payload
  };
}

export function addProductToBought(payload) {
  return {
    type: BUY_PRODUCT,
    payload
  };
}

export function deleteProduct(payload) {
  return {
    type: DELETE_PRODUCT,
    payload
  };
}

export function setCash(payload) {
  return {
    type: SET_CASH,
    payload
  };
}
