import { Link, useLocation } from "react-router-dom";

import "css/Header.css";
import { useState } from "react";

function HeaderLink({ to, children }) {
  const { pathname } = useLocation();

  const matches = pathname === to;

  return (
    <li
      className={`header__link`}
      style={{
        backgroundColor: matches ? 'blue' : 'white'
      }}
    >
      <Link to={to}>
        <span
          className="font-bold"
          style={{
            color: matches ? 'white' : 'black'
          }}
        >
          {children}
        </span>
      </Link>
    </li>
  )
}

function Header() {
  return (
    <header id="header" className="border-3">
      <ul
        id="header__links"
      >
        <HeaderLink to="/">
          Home
        </HeaderLink>
        <HeaderLink to="/shop">
          Shop
        </HeaderLink>
        <HeaderLink to="/about">
          About
        </HeaderLink>
      </ul>
    </header>
  );
}

export default Header;
