import 'css/Products.css';
import { useState } from 'react';
import { connect, useDispatch, useSelector } from 'react-redux';
import { addProductToBought, setCash } from 'store/actionCreators';
import AddProduct from './AddProduct';
import Alert from './Alert';

function Products({ products }) {
  const [showAlert, setShowAlert] = useState(false);
  const cash = useSelector(state => state.user.cash);
  const dispatch = useDispatch();

  const buyProduct = (name, price) => {
    if (cash >= price) {
      dispatch(addProductToBought({ name })); 
      dispatch(setCash({ to: cash - price }));
    } else {
      setShowAlert(true);
    }
  };

  const renderedProducts = products.map(product => (
    <li className="products__product" key={products.indexOf(product)}>
      <h2>{product.name}</h2>
      <br/><hr/><br/>
      <div
        className="product__product-info"
      >
        Price: <span
          className="text-bold"
          style={{
            color: cash >= product.price ? 'darkgreen' : 'red',
            fontSize: '20px'
          }}
        >
          {product.price}$
        </span>
      </div>
      <button
        className="product__buy-btn border-none"
        onClick={() => buyProduct(product.name, product.price)}
      >
        Buy
      </button>
    </li>
  ));

  return (
    <div>
      <h2 className="mr-top-left">Add Product</h2>
      <AddProduct />
      <h2 className="mr-top-left">Buy product</h2>
      <ul className="products">
        {renderedProducts}
      </ul>
      <Alert 
        showAlert={showAlert}
        setShowAlert={setShowAlert}
      />
      <hr/>
    </div>
  );
}

const mapStateToProps = (state) => ({
  products: state.products
});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(Products);
