import { useDispatch, useSelector } from "react-redux";
import { setCash } from "store/actionCreators";

import "css/Cash.css";

function Cash(props) {
  const cash = useSelector(state => state.user.cash);
  const dispatch = useDispatch();

  const addCash = () => {
    dispatch(setCash({ to: cash + 100 }));
  };

  return (
    <div id="cash">
      <span id="cash__label">
        Cash: {cash}$
      </span><br/>
      <button onClick={addCash} id="cash__buy-btn">
        Add 100$
      </button>
    </div>
  )
}

export default Cash;
