import "css/Alert.css";

function Alert({ showAlert, setShowAlert}) {
  const close = () => {
    setShowAlert(false);
  };

  return (
    <div
      id="alert"
      className={`
        border-3
        ${showAlert ? 'alert-visible' : 'alert-hidden'}
        ${showAlert ? 'alert-animate' : ''}
      `}
    >
      <h3>You don't have enought cash!</h3>
      <button
        className="font-bold border-none"
        id="alert__close-btn"
        onClick={close}
      >
        CLOSE
      </button>
    </div>
  )
}

export default Alert;
